td {
    width: 25%;
    height: 70px;
    border: 1px solid #ccc;
    text-align: center;
    font-size: 26px;
}

td:first-child {
    width: 10%;
}

 .off td {
    background: #eee;
  color: #ccc;
}

.del_btn {
    margin-left: 10px;
    background-color: firebrick;
}

@media screen and (max-width: 800px) {
    td {
        font-size: 17px;
        height: 45px;
    }

    td:first-child {
        width: 3%;
    }

    .show_btn {
        padding: 5px;
        font-size: 11px;
    }

    .del_btn {
        margin-left: 3px;
        padding: 5px;
        font-size: 11px;
    }
}